.teacher-social {
  width: 39px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @media (max-width: 1023px) {
    width: 25px;
    margin-right: 11px;
    margin-top: -3px;
  }
}

.teacher-social__link {
  color: inherit;
  text-decoration: none;

  @media (max-width: 767px) {
    position: absolute;
  }

  &:first-child {
    @media (max-width: 767px) {
      top: 13px;
      right: 20px;
    }
  }

  &:nth-child(2) {
    @media (max-width: 767px) {
      top: 45px;
      right: 20px;
    }
  }
}

.teacher-social__icon--vk {
  @media (max-width: 1023px) {
    width: 9px;
  }

  @media (max-width: 767px) {
    width: 17px;
  }
}

.teacher-social__icon--tw {
  margin-top: 6px;

  @media (max-width: 1023px) {
    width: 11px;
  }

  @media (max-width: 767px) {
    width: 18px;
  }
}
